	module.exports = {"data":{"markdownRemark":{"html":"<h3>Submission Requirements: Two-Factor Authentication</h3>\n<p>Programs can now <a href=\"https://docs.hackerone.com/programs/submission.html\">require</a> hackers to have two-factor authentication enabled in order to submit new reports to their program.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/submission-2-b502135c213cb9b300260315a5de2aea-33d3c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 341px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 12.112676056338028%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAACCAYAAABYBvyLAAAACXBIWXMAABcSAAAXEgFnn9JSAAAAUklEQVQI13XMywmAMBAE0PTfjVWkBDvwpGQ3B/eXMKIQUdDDg2EGJpkZROSmqp/5T7hh5cCyBdwd6TwkIjDzpdb68tyG0RVi9NiR54YpB3pvOAAKwZnwhrSySQAAAABJRU5ErkJggg=='); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"submissions\"\n        title=\"\"\n        src=\"/static/submission-2-b502135c213cb9b300260315a5de2aea-213a0.png\"\n        srcset=\"/static/submission-2-b502135c213cb9b300260315a5de2aea-4cb2e.png 125w,\n/static/submission-2-b502135c213cb9b300260315a5de2aea-a9aa6.png 250w,\n/static/submission-2-b502135c213cb9b300260315a5de2aea-213a0.png 500w,\n/static/submission-2-b502135c213cb9b300260315a5de2aea-33d3c.png 710w\"\n        sizes=\"(max-width: 341px) 100vw, 341px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>We've also renamed the <b>Signal Requirements</b> page under <b>Settings > Program > Hacker Management</b> to now be called <b>Submission</b>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/submission-c5955d47fc9b2d6d8f9ae61467f84242-b48be.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 187px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 50.80213903743316%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"submissions\"\n        title=\"\"\n        src=\"/static/submission-c5955d47fc9b2d6d8f9ae61467f84242-b48be.png\"\n        srcset=\"/static/submission-c5955d47fc9b2d6d8f9ae61467f84242-31c20.png 125w,\n/static/submission-c5955d47fc9b2d6d8f9ae61467f84242-c6ac5.png 250w,\n/static/submission-c5955d47fc9b2d6d8f9ae61467f84242-b48be.png 374w\"\n        sizes=\"(max-width: 187px) 100vw, 187px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Embedded Submission Form</h3>\n<p>Programs can now embed the HackerOne report submission form onto their own website. This enables hackers to submit reports without having to create an account on HackerOne. Learn more <a href=\"https://docs.hackerone.com/programs/embedded-submissions-form.html\">here</a>.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-228ef.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 51.04477611940299%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"embedded-submissions\"\n        title=\"\"\n        src=\"/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-e7ffa.png\"\n        srcset=\"/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-49813.png 125w,\n/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-dc8a5.png 250w,\n/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-e7ffa.png 500w,\n/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-dda24.png 750w,\n/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-c5f18.png 1000w,\n/static/embedded-submissions-88de925f0e3d581fa790e44e393ba46a-228ef.png 1340w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/changelog/2018/september","title":"September 2018"}}},"pathContext":{}}

